.floaterToolTip{
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    -webkit-flex-direction:column;
    flex-wrap: nowrap;
    justify-content: center;
    z-index: 3;
    background-color: rgb(32, 32, 32);
    opacity: 0.8;
    max-width: 200px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }
  .floaterToolTip>.person-name{
    font-weight: 600;
    font-size: calc(10px + 0.3vw);
    background-color: black;
  }
  .floaterToolTip>.job-title{
    text-transform: uppercase;
    font-weight: 500;
    font-size: calc(8px + 0.3vw);
  }
  .floaterToolTip>.salary-paid{
    font-weight: 500;
    font-size: calc(10px + 0.3vw);
    background-color:black;
  }
  .floaterToolTip>.taxable-benefits{
    font-weight: 500;
    font-size: calc(10px + 0.3vw);
  }

  /* DESKTOP LAYOUT */
.side-graph.browserView{
    /* border: 2px solid red; */
    width: 100%;
    z-index: 5;
    overflow: visible;
    display: flex;
    display: -webkit-flex;
    /* flex-basis: 25%; */
    /* -webkit-flex-basis:25%; */
    max-height: 30vh;
    margin: 1em 0em 0em 0em;
    flex-basis: 30%;
  }

  @media only screen and (max-width: 800px) {
    .side-graph.browserView{
      max-height: 10vh;
    }
  }

  
  .side-graph-wrapper.browser{
    /* border: 2px solid green; */
    width: 100%;
    height:100%;
    overflow: visible;
  }
  
  .side-graph-title.browser{
    display: flex;
    display: -webkit-flex;  
    align-items: flex-end;
  }
  .side-graph-title.browser>h4{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 600;
    text-transform: uppercase;
    font-size: calc(10px+0.5vw);
  }
  
  .side-graph-svg.browser{
    width:100%;
    overflow: visible;
  }

  /* MOBILE LAYOUT */
  .side-graph.mobileView{
    width: 100%;
    /* background-color: rgba(237, 100, 196, 0.267); */
    height: 50vh;
    overflow: visible;
    flex-basis: 25%;
    }
  
  .side-graph-wrapper.mobile{
    /* border: 2px solid green; */
    width: 100%;
    height:100%;
    
  }
  .side-graph-title.mobile{
    display: flex;
    display: -webkit-flex;  
    align-items: flex-end;
  }
  .side-graph-title.mobile>h4{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 600;
    text-transform: uppercase;
    font-size: calc(10px+0.5vw);
  }
  .side-graph-svg.mobile{
    /* width:100%; */
    height: 100%;
