/* desktop and mobile common layout */
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  color: white;
}

body{
  overflow: visible;
  background-color: rgb(32, 32, 32);
  z-index: -2;
}

.bunch-of-logos{
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  -webkit-flex-direction:row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  -webkit-flex-wrap:wrap;
  width: 100%;
  /* border: 5px solid orange; */
  text-align: center;
}

.bunch-of-logos > .one-logo:hover> img{
  width:105%;
  transition: 500ms;
}

.bunch-of-logos > .one-logo > img{
  width: 100%;
  object-fit: cover;
  clip-path: circle(50%);
}

.deselect{
  opacity: 0.6;
  filter: grayscale(100%) invert(100%);
  /* transform: scale(0.6); */
}


/* -------------------------------------------------- */
/* desktop specific layout */
/* -------------------------------------------------- */

/* @media only screen and (max-width: 800px) {
  body{
    max-height: 190vh;
  }
} */

.app.browserView{
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  -webkit-flex-direction:row;
  min-height: 125vh;
  border: 5px dotted cyan;
}

@media only screen and (max-width: 800px) {
  .app.browserView{
    flex-direction: column;
    border:2px solid white;
  }
}


.header.browser{
  width:100%;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background-color:black;
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  -webkit-flex-direction:column;
  padding:2px 0px 2px 5px
}

.header.browser>h3{
  font-size: calc(18px + 0.5vw);
}
.header.browser>p{
  font-size: calc(12px + 0.5vw);
}

.separator{
  opacity: 0.5;
  width: 90%;
}

.window-container.browser{
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  -webkit-flex-direction:column;
  width: 80%;
  border:2px solid red
}

@media only screen and (max-width: 800px) {
  .window-container.browser{
    width: 100%;
  }
} 

.stat-selector.browser{
  display: flex;
  display: -webkit-flex;
  align-items: flex-end;
  -webkit-align-items:flex-end;
  width: 100%;
  margin-left: 0%;
  justify-content: space-around;
  -webkit-justify-content:space-around;
  background-color: black;
}

.stat-button.browser{
  font-size: calc(10px + 0.2vw);
  background-color: black;
  color: white;
  border-radius: 10px;
  padding:1px 4px 1px 4px;
}

.controls-container.browser{
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  -webkit-flex-direction:column;
  width: 20%;
}



@media only screen and (max-width: 800px) {
  .controls-container.browser{
    width:100%;
    flex-direction: row;
  }
}

.logos-header.browser{
  width: 100%;
  /* display: flex; */
  /* justify-content: space-around; */
  margin:0.5em 0 1em 0;
  /* padding:1em 1em 0 1em; */
  display: flex;
  display: -webkit-flex;
  justify-content: center;
}

.logos-title.browser{
  font-size: calc(10px+2vh);
  display: flex;
  display: -webkit-flex;
  /* flex-basis: 100%; */
  /* max-width: 190px; */
  padding: 1px 3px 1px 3px;
  text-transform: uppercase;
  border-radius: 20px;
  background-color: rgba(43, 43, 43, 0.931);
  color:rgb(133, 131, 131);
  /* justify-content: center; */
}

.logos-title.browser.selected{
  background-color: black;
  color:white
}

.logos-all.browser{
  font-size:10px;
  background-color: black;
  color: white;
  border-radius: 10px;
  padding: 0.2em;
}

.one-logo.browser{
  /* display: flex; */
  /* display: -webkit-flex; */
  flex-basis: 14%;
  -webkit-flex-basis:14%;
  /* border: 1px solid green; */
  transition: 500ms;
  object-fit: cover;
  margin:0.2em 0.2em 0.2em 0.2em
}

@media only screen and (max-width: 800px) {
  .one-logo.browser{
    flex-basis: 10%;
    -webkit-flex-basis:10%
  }
}

.bunch-of-logos.browser{
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  -webkit-flex-direction:row;
  /* justify-content: center; */
  /* align-items: center; */
  flex-wrap: wrap;
  -webkit-flex-wrap:wrap;
  width: 100%;
  /* border: 5px solid orange; */
  /* text-align: center; */
}

.logo-tooltip{
  font-size:calc(12px + 0.1vh);
  color: white;
  background-color: black;
  opacity: 0.7;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  z-index: 5;
  white-space: nowrap;
  overflow: hidden;
}



/* -------------------------------------------------- */
/* -------------------------------------------------- */
/* MOBILE SPECIFIC LAYOUT */
/* -------------------------------------------------- */
/* -------------------------------------------------- */


.app.mobileView{
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  -webkit-flex-direction:column
  /* border: 4px solid black; */
}

.header.mobile{
  margin-left:5%;
  width: 90%;
}

.header.mobile > h4{
  width:100%;
  font-size: calc(18px + 2vw);
  color: lightblue;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.header.mobile > p{
  font-size: calc(15px + 1vw);
  color: lightcoral;
  font-weight: 300;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.controls-container.mobile{
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  -webkit-flex-direction:column;
  width: 100%;
  /* border: 2px solid red; */
}

.university-logos.mobile, .college-logos.mobile{
  padding: 1em 1em 0em 1em;
}

.logos-header.mobile{
  border-top: 1px solid white;
  padding-top: 1em;
  display: flex;
  justify-content: center;
}

.logos-title.mobile{
  display: flex;
  display: -webkit-flex;
  text-transform: uppercase;
  border-radius: 20px;
  background-color: rgba(43, 43, 43, 0.931);
  color:rgb(133, 131, 131);
  font-weight: 600;
  text-transform: uppercase;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: calc(12px + 1vw);
  
}

.logos-title.mobile.selected{
  background-color: black;
  color:white
}

.logos-all.mobile,.toggle-show.mobile{
  background-color: black;
  font-size:calc(12px + 1vw);
  text-transform: uppercase;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  border-radius: 50%;
}

.one-logo.mobile{
  display: flex;
  display: -webkit-flex;
  flex-basis: 10%;
  -webkit-flex-basis:10%;
  /* border: 1px solid red; */
  margin: 2px;
  transition: 500ms;
  object-fit: cover;
}

.display{
  display: none;
}

.stat-selector.mobile{
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  -webkit-flex-direction:row;
  width: 100%;
  justify-content: space-around;
  -webkit-justify-content:space-around;
  margin:4em 0.5em 0.5em 0.5em
}

.stat-button.mobile{
  display: flex;
  display: -webkit-flex;
  background-color: black;
  color: white;
  border:1px solid rgb(190, 158, 16);
  border-radius: 10px;
  font-size:calc(10px + 1vw);
  padding: 0.2em;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

}


