    .stream-xAxis{
        opacity: 0.5;
    }

    .stream-yAxis{
        opacity: 0.5;
    }
    .stream-yAxis > .domain{
        stroke-width: 0;
    }

  /* DESKTOP LAYOUT */
    .streamgraph.browserView{
    display: flex;
    flex-direction: column;
    display: -webkit-flex;
    overflow: hidden;
    z-index: 2;
    padding:1em 1em 1em 1em;
    /* margin-top: 5%; */
    flex-basis: 100%;
    -webkit-flex-basis:100%;
    border:2px dotted yellow;
    justify-content: space-around;
    }

    /* @media only screen and (max-width: 800px) {
    .streamgraph.browserView{
        max-height: 90vh;
    }
    } */

    .streamgraph-wrapper.browser{
    width: 100%;
    display: flex;
    display: -webkit-flex;
    border:2px solid green;
    flex-direction: column;
    -webkit-flex-direction:column;
    overflow: visible;
    align-items: center;
    flex-basis: 70%;
    margin: 0em 0em 1em 0em;
    }

    .slider.browser{
    width: 98%;
    flex-basis: 2%;
    -webkit-flex-basis:2%;
    background-color: red;
    }

    .slider-group>.tick>text{
    font-size:20px;
    fill:white
    }

    .streamgraph-svg.browser{
    width: 100%;
    height: 100%;
    /* flex-basis: 95%; */
    /* -webkit-flex-basis:95%; */
    display: flex;
    display: -webkit-flex;
    overflow: visible;
    flex-basis: 98%;
    }
  
  /* MOBILE LAYOUT */
    .streamgraph.mobileView{
        display: flex;
        display: -webkit-flex;
        width: 100%;
        height: 100%;
        border:2px dotted red;
        flex-direction: column;
        -webkit-flex-direction:column;
        padding:1em 0.5em 1em 0.5em
    }
    
    .streamgraph-wrapper.mobile{
        min-height: 120vh;
        width: 100%;
        /* min-height: 500px; */
        display: flex;
        display: -webkit-flex;
        overflow: visible;
        flex-direction: column;
        -webkit-flex-direction:column;
        flex-basis: 75%;
    }
    
    
    .slider.mobile{
        width:98%;
        flex-basis:1%;
        -webkit-flex-basis:1%;
        background-color: red;
    }
    
    .streamgraph-svg.mobile{
        width: 100%;
        overflow: visible;
        display: flex;
        display: -webkit-flex;
        flex-basis: 95%;
        -webkit-flex-basis:95%;
    }
    