*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    color: white;
  }

  
.tooltip-yAxis-1>.domain{
    stroke-width: 0;
  }
  .tooltip-yAxis-1>.tick>line{
    opacity: 1;
  }
  .tooltip-yAxis-1>.tick>text{
    font-size: calc(11px + .2vh);
    color: rgba(35, 35, 94, 0.959);
    font-weight: 600;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    /* transform: translate(0,-0.6em); */
  }
  
  .tooltip-yAxis-1>.tick:first-child>line{
    opacity: 0.8;
    /* stroke-color:red */
  
  }
  /* .tooltip-yAxis-1>.tick:first-child>text{ */
    /* font-size: 1em; */
    /* transform: translate(0,-0.6em); */
  /* } */

  /* DESKTOP LAYOUT */
  .tooltip.browserView{
    background-color: rgba(218, 218, 218, 0.63);
    width: 20%;
    min-width: 300px;
    max-width: 360px;
    min-height: 150px;
    max-height: 180px;
    height:20%;
    position: absolute;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    -webkit-flex-direction:column;
    flex-wrap: nowrap;
  }  
  
  .tooltip-header.browser{
    width: 100%;
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
    /* border:1px solid yellow; */
    font-size: calc(15px + .1vw);
    font-weight: 600;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    padding: 2px 5px 0px 5px;
  
  }  
  .tooltip-body.browser{
    width: 100%;
    background-color: rgba(223, 223, 223, 0.925);
  }
  .tooltip-wrapper.browser{
    /* border:2px solid red; */
    display: flex;
    display: -webkit-flex;
  }  
  
  .tooltip-svg.browser{
    display: flex;
    display: -webkit-flex;
    /* padding:2px 5px 0px 5px; */
    background-color:rgba(223, 223, 223, 0.925);
    height:100%;
    width: 100%;
  }  
  
  .tooltip-footer.browser{
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    border:1px solid yellow;
    font-size: calc(8px + .4vw);
    font-weight: 600;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }
  
  .tooltip-footer.browser > h4{
    color: #751f05e1;
  }

  /* MOBILE LAYOUT */
  .tooltip.mobileView{
    width:50%;
    height:25%;
    min-width: 200px;
    min-height: 80px;
    max-width: 300px;
    max-height: 120px;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    -webkit-flex-direction:column;
    flex-wrap: nowrap;
    position:absolute;
    background-color: rgba(218, 218, 218, 0.63);
  }
  
  .tooltip-header.mobile{
    width: 100%;
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
    font-size: calc(10px + 0.4vw);
    font-weight: 600;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    padding-left: 2px 5px 0px 5px;
  }
  
  .tooltip-wrapper.mobile{
    width: 100%;
  }
  
  .tooltip-svg.mobile{
    width: 100%;
  }
  
  .tooltip-footer.mobile{
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    border:1px solid yellow;
    font-size: calc(10px + .2vw);
    font-weight: 400;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }
  
  .tooltip-footer.mobile > h4{
    color: #751f05e1;
  }
  
  